import React, {useEffect} from 'react'
import L from 'leaflet' 
import 'leaflet.heat'
import text from '../data/ran_yaqui.json'
import Wkt from 'wicket'
import 'proj4leaflet'
import 'proj4'

const Map = () => {
    var wkt = new Wkt.Wkt();
    const calculateStats = (map, date, baseMap) => {
        const data = text
        const addressPoints = data.map(element => {
            try{
                const centroid = JSON.parse(element.centroid).coordinates
                const wkt = element.wkt
                const stats = JSON.parse(element.stats).C0
                // const dates = stats.map(e => {
                //     return new Date(e.date)
                // })
                const ndvis = stats.map(e => {
                    return e.basicStats.mean
                })
                const maxNdvi = Math.max(...ndvis)
                // const target = new Date(date)
                // const index = nearest(dates, target)
                return [centroid[1], centroid[0], maxNdvi, wkt]
            }
            catch {
                return []
            }
        })
        let group = []
        let points = addressPoints
            ? addressPoints.map((p) => {
                if (p[3]) {
                    let json = wkt.read(p[3]).toJson()
                    group.push({
                        "type": "Feature",
                        "properties": {},
                        "geometry": json
                    })
                }
                if (p[0]) return [p[0], p[1], p[2]];
                else return null
            })
            : [];
        const fullJson = {
            "type": "FeatureCollection",
            "crs": {
                "type": "name",
                "properties": {
                    "name": "urn:ogc:def:crs:EPSG::3857"
                }
            },
            "features": group
        }
        points = points.filter(p => p)
        const lowPoints = points.filter(p => p[2] < 0.4)
        const midPoints = points.filter(p => p[2] >= 0.4 && p[2] < 0.8)
        const highPoints = points.filter(p => p[2] >= 0.8)

        console.log(`Menores a 0.4 ${lowPoints.length}
        Mayores a 0.4 ${midPoints.length}
        Mayores a 0.8 ${highPoints.length}
        Total: ${ lowPoints.length + midPoints.length + highPoints.length}`)

        const lowHeatmapLayer = L.heatLayer(lowPoints.map(p => [p[0], p[1], 1]),
            {   
                minOpacity: 0.15,
                radius: 30, 
                gradient: { 0.4: "#fee0e0", 0.65: "#ff0000", 0.8: "#8a0202"}
            });
        const midHeatmapLayer = L.heatLayer(midPoints.map(p => [p[0], p[1], 1]),
            {
                // blur: 5,
                minOpacity: 0.15,
                radius: 30, 
                gradient: { 0.4: "#e0e1fe", 0.65: "#3947ff", 0.8: "#03118b"}
            });
        const highHeatmapLayer = L.heatLayer(highPoints.map(p => [p[0], p[1], 1]),
            {
                minOpacity: 0.15,
                radius: 30, 
                gradient: { 0.4: "#e1fee0", 0.65: "#12c624", 0.8: "#027400"}
            }).addTo(map);
        const polygonsLayer = L.Proj.geoJson(fullJson)

        const satelliteLayer = L.tileLayer('http://{s}.google.com/vt/lyrs=s,h&x={x}&y={y}&z={z}',{
            maxZoom: 20,
            subdomains:['mt0','mt1','mt2','mt3']
        }
  );

        const baseMaps = {
            "Mapa": baseMap,
            "Imagen Satelital": satelliteLayer
        }

        const overlayMaps = {
            "Zonas de desempeño bajo": lowHeatmapLayer,
            "Zonas de desempeño medio": midHeatmapLayer,
            "Zonas de desempeño alto": highHeatmapLayer,
            "Predios RAN": polygonsLayer
        }

        L.control.layers(baseMaps, overlayMaps, {collapsed: false}).addTo(map);
    } 

    useEffect(() => {
      var map = L.map("map").setView([27.330812, -110.021760], 11);
  
      const baseMap = L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        attribution:
          '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
      }).addTo(map);

      calculateStats(map, '2021-03-26', baseMap)

    }, []);
  
    return <div id="map" style={{ height: "90vh" }}></div>;
  }

export default Map