import React from 'react'
import logo from '../images/sinecta_logo.png'

const Header = () => {
    return <>
    <main className="header">
        <img className="logo" src={logo} alt="logo"/>
        <div className="text-container">
            <h1 className="header-text">Clasificación de zonas por desempeño</h1>
            <h3 className="header-text">Valle del Yaqui</h3>
            <h5 className="header-text">Junio 2020 - Junio 2021</h5>
        </div>
    </main>
    </>
}

export default Header